<!-- UI -->
<template>
  <div class="indigo lighten-4 pa-8">
    <v-carousel
      cycle
      show-arrows-on-hover
      hide-delimiters
      hide-delimiter-background
      delimiter-icon="mdi-minus"
      vertical
      vertical-delimiters>
      <v-carousel-item 
        v-for="(item, i) in items" 
        :key="i"
        :src="item.img1"
        @click="showDetail(item.pcolorid)">
        <h2 class="ma-3" style="font-weight: bold"></h2>
        <div class="mt-4 ml-4" style="font-weight: bold">{{item.bname}}</div>
        <div class="ml-4">{{item.pname}}</div>
        <div class="ml-4">{{item.pprice | comma}}</div>
      </v-carousel-item>
    </v-carousel>
    <v-select
    :items="d1names"
    v-model="d1name"
    @change="change()">
    </v-select>
  </div>
</template>

<script>
import main from "@/apis/product/main";

export default {
  //  (devtools  )
  name:"New",
  //   
  components:{
  },
  filters: {
    comma(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  //  
  data: () => ({
    items: null,
    d1names:null,
    d1name:"WOMEN",
    }),
  //  
  methods:{
    showDetail(pcolorId) {
      this.$router.push({
        name: "productdetail",
        params: {
          initPcolorid: pcolorId
        }
      });
    },
    change() {
      main.getNewItems(this.d1name).then(response => {
      this.items = response.data;
    });
    }
  },
  beforeCreate() {
    main.getD1Names()
    .then(response => {
      this.d1names = response.data;
    });
    main.getNewItems().then(response => {
      this.items = response.data;
    });
  },

};
</script>

