<!--  UI  -->
<template>
  <modal-dialog-template>
    <template v-slot:header> 
      
    </template>

    <template v-slot:body>
      <div v-if="loading" class="mt-3">
        <div class="spinner-border text-dark" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div> 
      <div v-if="!loading">{{message}}</div>
    </template>

    <template v-slot:footer> 
      <b-button variant="primary" class="mt-2" v-on:click="closeAndPrev()"></b-button>
    </template>
  </modal-dialog-template>
</template>


<script>
import ModalDialogTemplate from '@/components/dialog/ModalDialogTemplate.vue';

export default {
  //  (devtools  )
  name:"AlertDialog",
  //   
  components:{
    ModalDialogTemplate
  },
  //    
  data: function() {
    return {
      // ModalDialogTemplate
    };
  },
  //   
  methods: {
    closeAndPrev() {
      //console.log("component7 closeAndPrev");
      this.$emit('close');
      //console.log("component7 close");
      this.$router.go(-1);
    }
  },
  props: [ "message", "loading" ]
};
</script>

