import axios from "axios";

const instance = axios.create({
  baseURL: 'http://localhost:81'
  //baseURL:'http://kosa1.iptime.org:50320'
});

/*    session token     */
instance.interceptors.request.use(function (config) {
  const token = sessionStorage.getItem('authToken');
  config.headers.Authorization =  token ? `Bearer ${token}` : '';
  return config;
});
/*    session token     */

function selectAllEvent() {
  return instance.get("/event");
}

function selectEvent(eno) {
  console.log("selectEvent " + eno);
  return instance.get(`/event/${eno}`);
}

function selectForMain() {
  return instance.get("/event/main");
}

function getCoupon(eno) {
  return instance.post("/event/coupon/"+eno);
}

export default {
  selectAllEvent,
  selectEvent,
  selectForMain,
  getCoupon,
}