<!-- UI -->
<template>
  <div class="mt-2">
    <div v-for="(coupon,index) in coupons" :key="index">
      <v-card v-if="coupon.cuseDate===null" class="mt-2">
        <div class="row no-gutters">
          <div class="col-3">
            <v-img  :src="coupon.ethumbnail" width="95%" height="95"></v-img>
          </div>
          <div class="col-9">
            <v-card-title class="ma-2 pa-0" style="font-weight: bold; font-size:18px;">{{coupon.etitle}} </v-card-title>
            <v-card-text class="ma-2 pa-0">
             : {{coupon.cissueDate}} ~ {{coupon.cexpireDate}}
          </v-card-text>
          </div>
        </div>
      </v-card>
      <v-card v-else class="mt-2" disabled>
        <div class="row no-gutters">
          <div class="col-3">
            <v-img  :src="coupon.ethumbnail" width="95%" height="95"></v-img>
          </div>
          <div class="col-9">
            <v-card-title class="ma-2 pa-0" style="font-weight: bold; font-size:18px;">{{coupon.etitle}} </v-card-title>
            <v-card-text class="ma-2 pa-0">
              
          </v-card-text>
          </div>
        </div>
      </v-card>
    </div>
  </div>
</template>

<script>
import userApi from "@/apis/member/user";

export default {
  //  (devtools  )
  name:"Coupon",
  //   
  components:{
  },
  //  
  data: function() {
    return {
      coupons:[],
    };
  },
  //  
  methods:{
  },
  async created() {
    await userApi.getUserCoupon()
      .then((response)=> {
        this.coupons = response.data;
        console.log(this.coupons);
      }).catch((error)=>{
        console.log(error);
      });
  }
}
</script>

