<template>
  <div style="background-color: #f7f1e9;">
    <v-toolbar
      flat
      color="grey lighten-1">
      <v-toolbar-title style="font-weight: bold; font-size:18px;"> </v-toolbar-title>
    </v-toolbar>

    <div class="mt-4">
      <div class="text-center mb-4"><img v-bind:src="`${userGrade.gimg}`" width="90px" height="90px" /></div>
      <p class="text-center" style="font-weight: bold; font-size:15px;">  {{userGrade.glevel}}Level .</p>
      <p v-if="userGrade.glevel<5" class="mt-2 text-center">  {{userGrade.remain}} .</p>
    </div>

  </div>
</template>


<script>
import userApi from "@/apis/member/user";

export default {
  //  (devtools  )
  name:"userGrade",
  //   
  components:{
  },
  //  
  data: function() {
    return {
      userGrade:null,
    };
  },
  //  
  methods:{
  },
  async created() {
    await userApi.getUserGrade()
      .then((response)=> {
        this.userGrade = response.data;
        console.log(this.userGrade);
      }).catch((error)=>{
        console.log(error);
      });
  }
}
</script>

