export default {
  /*       */
  namespaced: true,
  /*   */
  state: {
    pageNo: 1,
    d1Name: "",
    d2Name: "",
    d3Name: "",
    pageItems: null
  },
  /*    (Getter) */
  getters: {
    getPageNo(state) {
      return state.pageNo;
    },
    getDepth(state) {
      return {
        "d1Name": state.d1Name,
        "d2Name": state.d2Name,
        "d3Name": state.d3Name
      }
    },
    getPageItems(state){
      return state.pageItems;
    }
  },
  /*     (Setter) */
  mutations: {
    setPageNo(state, pageNo){
      state.pageNo = pageNo;
    },
    setDepth(state, payload) {
      state.d1Name = payload.d1Name;
      state.d2Name = payload.d2Name;
      state.d3Name = payload.d3Name;
    },
    setPageItems(state, pageItems) {
      state.pageItems = pageItems;
    }
  },
  /*     (Setter) */
  actions: {},
};