<!-- UI -->
<template>
  <div>
    <v-toolbar
      flat
      color="grey lighten-1"
      class="text-center mt-4">
      <v-toolbar-title style="font-weight: bold; font-size:18px;">{{$store.getters["userStore/getUserId"]}} </v-toolbar-title>
    </v-toolbar>

    <div class="mt-4">
      <div class="text-center mb-4">{{userMileage}} M</div>
    </div>

  </div>
</template>
  
<script>
import userApi from "@/apis/member/user";

export default {
  //  (devtools  )
  name:"Mileage",
  //   
  components:{
  },
  //  
  data: function() {
    return {
      userMileage:null,
    };
  },
  //  
  methods:{
  },
  async created() {
    await userApi.getUserMileage()
      .then((response)=> {
        this.userMileage = response.data;
        console.log(this.userMileage);
      }).catch((error)=>{
        console.log(error);
      });
  }
}
</script>

