<!-- UI -->
<template>
  <div>
    <v-card class="mt-5 mb-8" outlined style="background-color: #f7f1e9;">
      <v-card-text>
        <v-container>
          <div v-for="(oneOrder, j) in allOrders" :key="j" >
          <v-row>
            <v-col cols="12">
              <div @click="goToOrderComplete(oneOrder.oid)" style="font-weight: bold; font-size: large"><span>  {{oneOrder.orderHistoryItems[0].odate}}</span><v-btn style="float: right; background-color: #ffffff;"><v-icon>mdi-chevron-right</v-icon></v-btn></div> 

            </v-col>
            <v-divider/>
             
          </v-row>
          <div v-for="(oneItem, i) in oneOrder.orderHistoryItems" :key="i">

            <div class= "card mb-2"  style="background-color: #f7f1e9;">

            <v-row>
              
              <v-col cols="12">

              <div style=" font-weight: bold; color:blue; float: right; opacity: 0.5" >{{oneItem.ostatus}}</div> 

            </v-col>
              <v-col cols="4">
                <v-img
                  class="mr-2"
                  :src="oneItem.img1"
                  width="100"
                  height="100"
                />
              </v-col>
              <v-col cols="6">

                
                  <div style="font-weight: bold; ">{{oneItem.bname}}</div>
                  <div>{{oneItem.pname}}</div>
                  <div>{{oneItem.ccode}} | {{oneItem.scode}} | {{oneItem.ocount}} </div>
                  <div style="font-weight: bold; font-size: large">{{oneItem.totalPrice | comma}}</div>
                

              </v-col>
              <v-col cols="2"> </v-col>
            </v-row>
            <button type="button" class="btn btn-secondary" style="color:#000051; background-color: #fdeeda; font-weight: bold;">    </button>
            
          </div>
          </div>
          <v-divider/>
          </div>
        </v-container>
      </v-card-text>
    </v-card>
  </div>
</template>

<style scoped>
/* .container {
    border: 1px solid green;
  }
  .row {
    border: 1px solid red;
  }
  .col {
    border: 1px solid blue;
  } */
</style>

<script>

import orderlist from "@/apis/order/orderlist"
import orderform from "@/apis/order/orderform";

export default {
  //  (devtools  )
  name: "",
  //   
  components: {},
  //  
  data: function () {
    return {
      allOrders : [],
    };
  },
  filters: {
    comma(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  //  
  methods: {
    async goToOrderComplete(oid) {
      let orderCompleteItems = {};
      await orderform.orderComplete(oid)
        .then((response) => {
          orderCompleteItems = response.data;
        });
      console.log("orderCompleteItems", orderCompleteItems);
      this.$router.push({
        name:'ordercomplete',
        params: {
          initOrderItems: orderCompleteItems
        }
      });
    }

  },

  async created() {
    await orderlist.showAllOrder()
      .then((response) => {
        this.allOrders = response.data;
        console.log("this.allOrders =", this.allOrders);
      });
  }
};
</script>

