<template>
  <div style="background-color: #f7f1e9;">
    <v-toolbar
      flat
      color="grey lighten-1"
    >
      <v-toolbar-title style="font-weight: bold; font-size:18px;">   </v-toolbar-title>
    </v-toolbar>

    <v-tabs vertical class="mt-4" style="background-color: #f7f1e9;">
      <v-tab v-for="grade in grades" :key="grade" style="background-color: #f7f1e9;">
        {{grade.glevel}} Level
      </v-tab>

      <v-tab-item v-for="grade in grades" :key="grade" style="background-color: #f7f1e9;">
        <div class="text-center mb-4"><img v-bind:src="`${grade.gimg}`" width="90px" height="90px" /></div>
        <p class="text-center" style="font-weight: bold; font-size:15px;"></p>
        <p v-if="grade.grate>0" class="mt-2 text-center">   ({{grade.grate}}% )</p>
        <p  class="mt-2 text-center">  </p>
        <p class="text-center" style="font-weight: bold; font-size:15px;">  </p>
        <p class="mt-2 text-center">{{grade.gnote}}</p>
      </v-tab-item>
    </v-tabs>
  </div>
</template>

<script>
import userApi from "@/apis/member/user";

export default {
  //  (devtools  )
  name:"gradeInfo",
  //   
  components:{
  },
  //  
  data: function() {
    return {
      grades:[],
    };
  },
  //  
  methods:{
  },
  async created() {
    await userApi.getGradeInfo()
      .then((response)=> {
        this.grades = response.data;
      }).catch((error)=>{
        console.log(error);
      });
  }
}
</script>

