<!-- UI -->
<template>
  <div style="background-color: #fdeeda;">
    <section>
      <div class="container mt-2">
        <div class="row row-cols-2 row row-cols-md-3 row-cols-xl-4 justify-content-center">
          <div v-for="(item, i) in wishItems" :key="i" class="mb-4">
            <v-card class="h-100 mx-auto" width="180" @click="showDetail(item.pcolorid)">
              <!-- Product image-->
              <div class="text-center">
                <div class="mb-1" >
                  <div class="wish-icon-div">
                      <v-btn icon @click="refresh()">
                        <v-icon color="red">mdi-heart</v-icon>
                      </v-btn>
                  </div>
                      <img :src="`${item.img1}`" width="100%"/>
                </div>

                <!-- Product details-->
                <v-card-text class="card-body p-2">
                  <div class="text-center">
                    <!--  -->
                    <div class="fw-bolder mb-1 brand_name" style="font-weight: bold">{{item.bname}}</div>
                    <!-- -->
                    <div class="fw-bolder mb-1 name">{{item.pname}}</div>
                    <!-- -->
                    <div class="fw-bolder mb-1 price">
                      <span>{{item.pprice | comma}}</span>
                    </div>
                  </div>
                </v-card-text>
              </div>
            </v-card>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import wishlist from "@/apis/member/wishlist";

export default {
  //  (devtools  )
  name:"Product",
  //   
  components:{
  },
  //  
  data: function() {
    return {
      wishItems:[],
    };
  },
  //  
  methods:{
    refresh() {
      this.$router.go();
    },
    showDetail(pcolorId) {
      this.$router.push({
        name: "productdetail",
        params: {
          initPcolorid: pcolorId
        }
      });
    },
  },

  filters: {
    comma(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  beforeCreate() {
    wishlist.wishItems()
      .then((response)=>{
        console.log(response.data)
        this.wishItems = response.data;
      });
  }
}
</script>

<!--  -->
<!--scoped   -->
<style scoped>
    .wish-icon-div {
      position: absolute;
      z-index: 2;
      color: whitesmoke;
      font-size: 20px;
      text-align: right;
      cursor: pointer;
      border-radius: 10rem;
      background-color: transparent;
      border: transparent;
      width: 99%;
      padding-top: 4px;
      padding-right: 4px;
  }

  .wish-icon-div :hover {
      color: red;
  }
</style>